<script>
    export default {
        name: "user-view",
        data() {
            return {
                model : {
                    payment : {},
                    transaction : {},
                },
                loading: true
            };
        },
        methods : {
            init()
            {
                this.loading = true;
                window.axios.get(`${window.location.origin}/admin/portal/ecocash/${this.$route.params.id}/view`).then((response) => {
                    this.model = response.data.body.model;
                }).finally(() => {
                    this.loading = false;
                });
            },

            action(action)
            {
                window.action(action , 'Ecocash Payment' , `${window.location.origin}/admin/portal/ecocash/${this.$route.params.id}/${action}`).then((response) => {
                    this.model = response.data.body.model;
                });
            },
        },
        mounted() {
            this.init();
        }
    }
</script>
<template>
    <div :class="['dimmer' , loading ? 'active' : '']">
        <div class="loader"></div>
        <div class="dimmer-content">
            <div class="row">
                <div class="col-lg-12">
                    <div class="card ribbon-box ">
                        <div class="card-body">
                            <div class="d-flex align-items-center mb-3 flex-wrap">
                                <h4 class="header-title m-0 flex-fill">Ecocash Payment Information</h4>
                                <div class="">
                                    <button  @click="init" type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-reload mr-1"></i> Refresh
                                    </button>

                                    <button v-if="(model.state === 'COMPLETED') && !model.reversed" @click="action('reverse')" type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-lightbulb-off mr-1"></i> Reverse Transaction
                                    </button>
                                </div>
                            </div>
                            <div v-if="model.transaction"  class="text-left">
                                <p>
                                    <strong class="mr-2">Transaction  :</strong>
                                    <router-link class="badge badge-light p-2 m-1 font-12" :to="`/admin/transactions/${model.transaction.id}/view`">Transaction #{{ model.transaction.id }}</router-link>
                                </p>
                            </div>
                            <p>
                                <strong class="mr-2">State : </strong>
                                <span>{{ model.state }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Phone :</strong>
                                <span>{{ model.phone }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Ecocash Refrence :</strong>
                                <span>{{ model.ecocash_reference }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Error :</strong>
                                <span>{{ model.error }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Reversed :</strong>
                                <span>{{ model.reversed }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Reversal Reference :</strong>
                                <span>{{ model.reversal_reference }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Reversal State :</strong>
                                <span>{{ model.reversal_state }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Created :</strong>
                                <span>{{ model.created_at }}</span>
                            </p>
                            <p>
                                <strong class="mr-2">Last :</strong>
                                <span>{{ model.updated_at }}</span>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
